        url('../fonts/volgacliff.woff') format('woff'),
        url('../fonts/volgacliff.ttf') format('truetype'),
        url('../fonts/volgacliff.svg#volgacliff') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^='icon-']:before,
[class*=' icon-']:before,
.jq-selectbox__trigger:before {
    font-family: 'volgacliff';
