// Типографика
.section {
    margin: 0;
    padding: 50px 0;
    background-color: $colorWhite;
    @include mobile {
        padding-top: 30px;
        padding-bottom: 30px;
    }

    &.gray {
        background: $colorGray;
    }

    .color-main {
        color: $colorMain !important;
    }

    .color-gray {
        color: $colorGray !important;
    }

    .color-error {
        color: $colorError !important;
    }

    .color-white {
        color: $colorWhite !important;
    }

    .text-align-center {
        text-align: center;
    }

    h1, .h1 {
        margin: 5px 0;
        font-size: 34px;
        line-height: 40px;
        font-weight: 700;
        color: $colorText;
    }

    p {
        margin: 15px 0;
        font-size: 16px;
        line-height: 1.8em;
        color: $colorText;
        font-weight: 400;

        &.big-text,
        .big-text {
            font-size: 24px;
            line-height: 1.5em;
        }

        &.medium-text,
        .medium-text {
            font-size: 14px;
            line-height: 1.5em;
        }

        &.small-text,
        .small-text {
            font-size: 12px;
            line-height: 1.5em;
        }

        .icon {
            display: inline-block;
            vertical-align: top;
            margin-right: 10px;
            margin-left: 10px;

            &:before {
                line-height: 1.5em;
            }
        }

        &:first-child {
            margin-top: 0;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    a {
        color: $colorLink;

        @include desktop {
            &:hover {
                text-decoration: none;
            }
        }
    }

    b, strong {
        color: #000000;
        font-weight: 700;
    }

    .line {
        display: block;
        margin: 0;
        height: 1px;
        background: $colorGray;
    }

    // переключатели
    .toggle-block {
        display: block;
        margin: 0;
        padding: 0;

        .toggle-caption {
            display: block;
            position: relative;
            margin: 0;
            padding: 0 60px 0 0;

            .arrow {
                display: block;
                position: absolute;
                z-index: 1;
                right: 0;
                top: 50%;
                margin: -6px 0 0 0;
                font-size: 12px;
                color: $colorGray;
            }
        }

        .toggle-button {
            display: block;
            margin: 0;
            padding: 0;
            cursor: pointer;
        }

        .toggle-content {
            display: none;
            margin: 0;
            padding: 0;
        }
        .open-text {
            display: block;
        }
        .close-text {
            display: none;
        }
        &.open {
            .arrow {
                transform: rotate(-180deg);
            }

            .toggle-content {
                display: block;
            }
            .open-text {
                display: none;
            }
            .close-text {
                display: block;
            }
        }

        &.mobile-no-toggle {
            @include mobile {
                .toggle-caption {
                    padding-right: 0;
                }
                .p-r-60 {
                    padding-right: 10px !important;
                }
                .toggle-button {
                    cursor: default;
                }
                .arrow {
                    display: none;
                }
                .toggle-content {
                    display: block;
                }
            }
        }
    }
}
